// css 파일 가져오기
@import './_reset.scss';


// css 변수
$mainColor : #ff0000;

.red{
  color: $mainColor;
}



// 기존 문법
// div.container h4 {
//     font-style: italic;
//   }
// div.container p{
//   font-weight: bold;
// }

// nestinf 문법
div.container {
  h4{
    font-style: italic;
  }
  p{
    font-weight: bold;
  }
}



// @extend .(클래스명);  // css 승계
.my-alert p{
  margin: 0;
}

.my-alert {
  background-color: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  margin: 20px 0 10px 0;
}

.my-alert-red{
  @extend .my-alert; //.my-alert 클래스를 승계
  background-color: red;
  color: white;
}


// @mixin, @include / css 함수 화
@mixin funcName( $padding, $color ) { // 함수의 정의
  background-color: #eeeeee;
  color: $color;
  padding: $padding;
  border-radius: 5px;
  margin: 20px 0 10px 0;
} 

.my-alert {
  @include funcName( 20px, black ) //  함수의 사용
}

.my-alert-none{
 display: none;
}


